@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@300;400;900&family=Rubik:wght@300;400;700;900&display=swap);
:root {
  font-size: 16px;

  --slab: 'Roboto Slab', serif;
  --sans: 'Rubik', sans-serif;

  --black: #000000ff;
  --charcoal: #2f4550ff;
  --darkblue: #586f7cff;
  --lightblue: #bfd7eaff;
  --white: #f4f4f9ff;
}

html {
  font-family: 'Rubik', sans-serif;
  font-family: var(--sans);
  background-color: #f4f4f9ff;
  background-color: var(--white);
  color: #000000ff;
  color: var(--black);
}
header {
  text-align: center;
  font-family: var(--slab);
  text-transform: uppercase;
  font-size: 1.6rem;
}

main {
  width: 33%;
  margin: 3rem auto;
  text-align: left;
}

#joke {
  font-size: 1.4rem;
  background-color: var(--lightblue);
  border-radius: 5px;
  padding: 0.4rem 1rem;
}

.actions {
  text-align: right;
  margin: 2rem 0;
}

.actions button {
  width: 100%;
  font-family: var(--slab);
  font-size: 1.4rem;
  font-weight: 700;
  padding: 1rem;
  border-radius: 5px;
  color: var(--white);
  background-color: var(--darkblue);
  border: 1px solid var(--darkblue);
  transition: 0.3s;
}

.actions button:hover {
  cursor: pointer;
  background-color: var(--lightblue);
  border: 1px solid var(--lightblue);
  color: var(--charcoal);
  transition: 0.3s;
}

footer {
  position: fixed;
  bottom: 0;
  left: 0;
  background-color: var(--charcoal);
  width: 100%;
  text-align: center;
  color: var(--white);
  font-size: 1rem;
  line-height: 0.4rem;
  text-transform: lowercase;
}

footer a, footer a:visited {
  color: var(--white);
}

footer a:hover {
  color: var(--lightblue);
}
